.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    width: 100%;
    background-color: #1A1B6B;
    padding: 10px 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
  }
    
.nav-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.nav-links ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 5px;
}

.nav-links a, .nav-links button {
  text-decoration: none;
  color: #ffffff;
  background-color: transparent;
  cursor: pointer;
  font-size: calc(10px + 0.4vw);
  transition: color 0.3s ease;
}

.nav-links a:hover, .nav-links button:hover {
  color: #e0e0e0;
}

.settings-dropdown {
  position: relative;
  display: inline-block;
}

.settings-dropdown .dropdown-content {
  display: none;
  position: absolute;
  background-color: #1A1B6B;
  min-width: 160px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border: 2px solid #ffffff;
  border-radius: 4px;
}

.settings-dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content button {
  color: rgb(55, 138, 171);
  padding: 12px 16px;
  text-align: left;
  display: block;
  width: 100%;
  background: none;
}

.dropdown-content button:hover {
  background-color: #36a7c0;
}

.notification-button {
  position: relative;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 24px;
}

.notification-button.glow {
  animation: glow 1s infinite alternate;
}

.notification-badge {
  position: absolute;
  top: -5px;
  right: -10px;
  background-color: red;
  color: white;
  border-radius: 50%;
  padding: 5px;
  font-size: 12px;
}

@keyframes glow {
  from {
    box-shadow: 0 0 5px red;
  }
  to {
    box-shadow: 0 0 20px red;
  }
}

    